.header {
    position: relative;
    box-shadow: rgb(0 0 0 / 7%) 0px 3px 4px 0px;
    width: 100%;
    z-index: 10;
}

.normalHeaderInner {
    position: relative;
    width: 1050px;
    margin: 0 auto;
}

.normalHeader {
    position: relative;
    width: 1050px;
    height: 156px;
    margin: 0px auto;
    letter-spacing: -0.3px;
}

.hidden {
    display: none;
}

/* TOP */
.top {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.top a {
    padding: 10px 0;
}

.top a:first-child {
    color: rgb(95, 0, 128);
}

.underImg {
    width: 8px;
    height: 5px;
    background-image: url(https://res.kurly.com/pc/ico/1908/ico_down_16x10.png);
    background-size: cover;
    background-position: center center;
    display: inline-block;
    margin-left: 5px;
    margin-bottom: 1px;
}

/* MIDDLE */
.middle {
    align-items: center;
    height: 63px;
    padding-top: 36px;
    display: flex;
    position: relative;
    /* justify-content: space-between; */
}

.logoContainer {
}

.modeChanger {
    display: flex;
}

.marketMode,
.beautyMode {
    flex-shrink: 0;
    font-size: 18px;
    font-weight: normal;
    line-height: 1.33;
    letter-spacing: normal;
    color: rgb(181, 181, 181);
    cursor: pointer;
    position: relative;
    margin-left: 20px;
}

.activedMarket,
.activedBeauty {
    flex-shrink: 0;
    font-size: 18px;
    font-weight: normal;
    line-height: 1.33;
    letter-spacing: normal;
    font-weight: 500;
    color: rgb(95, 0, 128);
    cursor: pointer;
    position: relative;
    margin-left: 20px;
}

.marketMode::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 14px;
    margin-top: 5px;
    margin-left: 11px;
    background-color: rgb(226, 226, 226);
}

.searchBar {
    position: absolute;
    left: 325px;
}

.searchInput {
    width: 370px;
    height: 45px;
    border: 1px solid rgb(95, 0, 128);
    border-radius: 6px;
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(247 247 247) 0px 0px 0px 1px inset;
    padding: 0 15px;
}

.searchInput:focus {
    outline-style: none;
}

.searchInput::placeholder {
    font-family: 'Noto Sans KR', 'malgun gothic', AppleGothic, dotum, sans-serif;
    font-size: 16px;
    letter-spacing: -0.33px;
    color: rgb(153, 153, 153);
}

.searchButton {
    background: url(data:image/svg+xml;base64,)
        0px 0px no-repeat;
    position: relative;
    width: 30px;
    height: 30px;
    margin: 10px;
    right: 50px;
    top: 7px;
}

.userBox {
    position: absolute;
    right: 0;
    display: flex;
}

.mapButton {
    background: url(data:image/svg+xml;base64,);
    width: 36px;
    height: 36px;
    margin-right: 20px;
    cursor: pointer;
}

.mapButton:hover {
    background: url(data:image/svg+xml;base64,);
}

.likeButton {
    width: 36px;
    height: 36px;
    margin-right: 20px;
    cursor: pointer;
    background: url(data:image/svg+xml;base64,);
}

.likeButton:hover {
    background: url(data:image/svg+xml;base64,);
}

.cartButton {
    display: block;
    width: 36px;
    height: 36px;
    cursor: pointer;
    background: url(data:image/svg+xml;base64,);
}

.cartButton:hover {
    background: url(data:image/svg+xml;base64,);
}

.cartCount {
    position: absolute;
    /* right: -4px; */
    top: -1px;
    min-width: 15px;
    height: 15px;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 10px;
    background-color: rgb(95, 0, 128);
    font-size: 10px;
    color: rgb(255, 255, 255);
    line-height: 15px;
    text-align: center;
    white-space: nowrap;
    font-family: 'Noto Sans', 'malgun gothic', AppleGothic, dotum, sans-serif;
}

/* BOTTOM */

.bottom {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 1050px;
    height: 56px;
    margin: 0px auto;
}

.category {
    display: flex;
    align-items: center;
    height: 100%;
}

.category:hover > .categoryText {
    color: rgb(95, 0, 128);
}

.categoryImg {
    width: 16px;
    height: 14px;
    margin-right: 14px;
}

.categoryText {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: -0.3px;
    color: rgb(51, 51, 51);
}

.categoryText:hover {
    color: rgb(95, 0, 128);
    cursor: pointer;
}

.navMenu {
    display: flex;
    margin-left: 30px;
}

.menuList {
    display: flex;
    justify-content: center;
    width: 150px;
    padding-top: 20px;
    line-height: 20px;
    text-align: center;
}

.menuList > span {
    height: fit-content;
    font-size: 16px;
    font-weight: 500;
    color: rgb(51, 51, 51);
    cursor: pointer;
    border-bottom: 1px solid transparent;
}

.menuList > span:hover {
    color: rgb(95, 0, 128);
    border-bottom: 1px solid rgb(95, 0, 128);
}

.delivery {
    display: flex;
    align-items: center;
}
.deliveryTag {
    display: flex;
    align-items: center;
    height: 32px;
    padding: 1px 15px;
    border-radius: 18px;
    border: 1px solid rgb(238, 238, 238);
    color: rgb(102, 102, 102);
    font-size: 13px;
    line-height: 16px;
    letter-spacing: -0.32px;
}

.delivery span {
    font-weight: 500;
    color: rgb(95, 0, 128);
}

/* SMALLHEADER */
.smallHeader {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    /* display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 56px;
    margin: 0px auto; */
    letter-spacing: -0.3px;
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(0 0 0 / 7%) 0px 3px 4px 0px;
    width: 100%;
}

.smallHeaderInner {
    width: 1050px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 56px;
    margin: 0px auto;
}

.smallHeader .category {
    margin-right: 33px;
    white-space: nowrap;
}

.smallHeader li {
    display: flex;
    justify-content: center;
    width: 120px;
    line-height: 20px;
    text-align: center;
    align-items: center;
    padding: 0;
}

.smallSearchBar {
    width: 227px;
    height: 36px;
    background-color: rgb(247, 247, 247);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 14px;
    border-radius: 6px;
    box-shadow: rgb(247 247 247) 0px 0px 0px 1px inset;
    border: none;
    margin-left: 15px;
}

.smallSearchBar input {
    width: 198px;
    height: 30px;
    padding: 0 22px 0 0;
    font-weight: 400;
    font-size: 12px;
    color: rgb(51, 51, 51);
    line-height: 18px;
    border: none;
    outline: none;
    background-color: inherit;
}

.smallSearchBar input::placeholder {
    font-size: 12px;
}

.smallSearchButton {
    background: url('data:image/svg+xml;base64,')
        0px 0px no-repeat;
    position: relative;
    width: 40px;
    height: 30px;
}

.smallHeader .userBox {
    position: relative;
    margin-left: 20px;
}
