.Review{
    padding: 40px 0;
}

.top{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.top span{
    padding: 8px 0px;
    color: rgb(51, 51, 51);
    font-size: 28px;
    line-height: 1.15;
    letter-spacing: -0.26px;
    font-weight: 500;
}

.top p{
    font-size: 16px;
    font-weight: normal;
    line-height: 1.45;
    letter-spacing: -0.2px;
    text-align: center;
    color: rgb(153, 153, 153);
    margin-top: 2px;
    margin-bottom: 30px;
}

.list{
    position: relative;
    width: 1050px;
    margin: 0px auto;
    display: flex;
}

.listContainer{  /* Q. mainbanner.css와 같은 구조인데 왜 여기선 listContainer가 아니라 list에 속성을 주어야하는지 */
    position: relative;
    width: 1050px;   
    margin: 0px auto;
    display: flex;
}

.imgContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 175px;
    height: 175px;
    outline: none;
}

.imgContainer img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 40px;
    line-height: 1.9;
    font-size: 16px;
    letter-spacing: -0.05em;
}

.bottom p{
    color: rgb(153, 153, 153);
    font-weight: 400;
    line-height: 1.9;
    font-size: 16px;
    letter-spacing: -0.05em;
    margin: 0;
}

.bottom a{
    color: rgb(51, 51, 51);
    font-weight: 500;
    line-height: 1.9;
    font-size: 16px;
    letter-spacing: -0.05em;
    margin: 0;
}